<template>
    <div class="lista-transacoes w-100">
    <div class="ma-3">
      <h1 class="text-h6 font-weight-bold text-white mb-5">Transações</h1>
    </div>
    <div class="nome-itens-lista d-flex align-center px-3 ma-3 rounded elevation-6">
      <div class="w-25">
        <span class="text-white font-weight-regular">Transação</span>
      </div>
      <div class="w-25">
        <span class="text-white font-weight-regular">Valor</span>
      </div>
      <div class="w-25">
        <span class="text-white font-weight-regular">Data</span>
      </div>
      <div class="w-25">
        <span class="text-white font-weight-regular">Tipo</span>
      </div>
    </div>
    <div class="container-transacoes">
      <div
      v-for="transacao in transacoes"
      :key="transacao"
      class="transacao-single ma-3 rounded px-3 ma-3">
      <div class="info-transacao w-100 d-flex align-center">
        <div class="w-25">
          <span class="text-white font-weight-regular">{{transacao.nome}}</span>
        </div>
        <div class="w-25">
          <span class="text-white font-weight-regular">R$ {{parseFloat(transacao.valor).toFixed(2).replace('.',',')}}</span>
        </div>
        <div class="w-25">
          <span class="text-white font-weight-regular">{{transacao.data}}</span>
        </div>
        <div class="w-25">
          <span class="text-white font-weight-regular">{{transacao.tipo}}</span>
        </div>
      </div>
      <div class="edit-transacao d-flex justify-end align-center w-100">
        <div class="excluir-transacao">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" xmlns:xlink="http://www.w3.org/1999/xlink" xmlns:svgjs="http://svgjs.com/svgjs" viewBox="0 0 512 512" style="enable-background:new 0 0 512 512" xml:space="preserve" class=""><g><g><path d="M349.176 476.38H162.208a52.792 52.792 0 0 1-52.701-47.705L79.228 125.024a17.621 17.621 0 0 1 17.567-19.403h317.793a17.65 17.65 0 0 1 13.087 5.806 17.658 17.658 0 0 1 4.48 13.597l-30.278 303.634a52.808 52.808 0 0 1-52.701 47.722zm-232.89-335.449 28.355 284.231a17.62 17.62 0 0 0 17.567 15.907h186.968a17.62 17.62 0 0 0 17.567-15.907l28.354-284.231z" data-original="#000000"></path><path d="M458.724 140.931H52.655a17.655 17.655 0 1 1 0-35.31h406.069a17.654 17.654 0 1 1 0 35.31zM255.69 405.757a17.654 17.654 0 0 1-17.655-17.655V193.895a17.656 17.656 0 0 1 35.31 0v194.207a17.654 17.654 0 0 1-17.655 17.655zM193.88 405.758a17.661 17.661 0 0 1-17.567-16.067l-17.655-194.206a17.654 17.654 0 0 1 15.978-19.174 17.706 17.706 0 0 1 19.191 15.996l17.655 194.206a17.66 17.66 0 0 1-4.556 13.505 17.65 17.65 0 0 1-13.046 5.74zM317.501 405.754a17.66 17.66 0 0 1-17.602-19.245l17.655-194.207c.901-9.71 9.375-16.701 19.192-15.995a17.653 17.653 0 0 1 15.977 19.173l-17.655 194.207a17.656 17.656 0 0 1-17.567 16.067z" data-original="#000000"></path><path d="M335.138 140.931H176.241a17.656 17.656 0 0 1-17.655-17.655v-70.62A17.655 17.655 0 0 1 176.241 35h158.897a17.656 17.656 0 0 1 17.655 17.655v70.621a17.656 17.656 0 0 1-17.655 17.655zm-141.241-35.31h123.586v-35.31H193.897z" data-original="#000000"></path></g></g></svg>
        </div>
      </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { useStore } from 'vuex'
import { db } from '@/firebase'
import { collection, onSnapshot} from "firebase/firestore";
import { onMounted } from 'vue';

const store = useStore()

let token = store.state.token

let transacoes = ref([])

onMounted(async () => {
  onSnapshot(collection(db, "transacoes"), (snapshot) => {
    snapshot.forEach((docTransaction) => {
      if(token.id === docTransaction.data().idUser) {
        transacoes.value.push(docTransaction.data())
      }
    })
  });
})

</script>

<style lang="scss" scoped>
.lista-transacoes {
    .nome-itens-lista {
      background-color: #4f5258;
      height: 50px;
      min-width: 400px;
    }

    .container-transacoes {
      overflow-x: auto;
      .transacao-single {
        min-width: 500px;
        background-color: #25272c;
        height: 50px;
        overflow: hidden;
        transition: .3s;

        &:hover {
          height: 100px;
        }
      }

      .info-transacao {
        min-height: 50px;
      }

      .edit-transacao {
        height: 50px;

        .excluir-transacao {
          svg {
            fill: rgb(101, 101, 101) !important;widows: 27px;height: 27px;
            transition: .3s;
            cursor: pointer;
            &:hover {
              fill: rgb(205, 24, 24) !important;
            }
          }
        }
      }
    }
  }
</style>